
.wrapper.fixed_layout .main-header{position:fixed;width:100%
}
.wrapper.fixed_layout .content-wrapper{padding-top:50px
}
.wrapper.fixed_layout .main-sidebar{position:fixed;height:100vh
}
@media (max-width: 767px){
.wrapper.hide_logo .main-header .logo{display:none
}
}
.logo-mini,.logo-lg{text-align:left
}
.logo-mini img,.logo-lg img{padding:.4em
}
.logo-lg img{display:-webkit-inline-box;width:25%
}
.user-panel{height:4em
}
hr.visible-xs-block{width:100%;background-color:rgba(0,0,0,0.17);height:1px;border-color:transparent
}



